<template>
  <section id="scroll" class="scroll center-news-worldCup bg-white border border-1 border-slate-300 rounded-sm mx-1" :style="{height:heightBox+'px'}">
    <div class="container mx-auto p-1">
      <div class="w-full h-auto">
        <slot name="teamName"><p class="text-sm text-right font-semibold my-1 text-slate-900 my-2">اخبار جام جهانی 2022</p></slot>
        <div class="w-full h-0.5 bg-slate-800 my-1"></div>
        <div class="flex flex-col space-y-2">
          <div v-for="items in myList" :key="items.news_id">
            <div v-for="value in items.data" :key="value.news_id">
              <div class="flex flex-row justify-between mb-3 p-2">
                <div class="grid grid-cols-1 space-y-5 w-8/12" :style="{height:heightText+'px'}">
                  <div class="w-full">
                    <p class="text-center font-semibold text-slate-800 text-right" :style="{fontSize:heightFont+'px'}">{{value.title}}</p></div>
                  <div class="flex flex-row justify-between w-full">
                    <div>
                      <p class="text-right text-md font-semibold text-slate-500">4 روز قبل</p>
                    </div>
                    <div class="flex flex-row">
                      <font-awesome-icon :icon="['fas','comment']" class="text-slate-500 text-sm mx-1"></font-awesome-icon>
                      <span class="text-sm text-slate-500">{{value.news_comment_count}}</span>
                      <font-awesome-icon :icon="['fas','eye']" class="text-sm text-slate-500 mx-1"></font-awesome-icon>
                      <span class="text-slate-500 text-sm mx-1">{{value.visit}}</span>
                    </div>
                  </div>
                </div>
                <div class="myImage w-3/12 h-full">
                  <img @error="notShow" :src="`${value.news_image[0].url}`" alt="" class="w-full h-full">
                </div>
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>
  </section>
</template>

<script>
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import{ref} from 'vue'
export default {
  name: "ExampleGetNews",
  props:{
    myList:{
      type:Array,
      required:true
    }
  },
  components:{FontAwesomeIcon},
  setup(){
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_default_news_image.png')
    const heightBox=ref(1200)
    const heightFont=ref(20)
    const heightText=ref(150)
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
      heightBox,heightFont,heightText,notFound,notShow,
    }
  }
}
</script>

<style scoped>
.scroll{
  height: auto;
  overflow-y: scroll;
  overflow-x: hidden;
}
#scroll::-webkit-scrollbar{
  width: 5px;
  background-color: #fff;
}
#scroll::-webkit-scrollbar-thumb{
  background-color: grey;
}
#scroll::-webkit-scrollbar-track{
  background-color: #fff;
  -webkit-box-shadow: inset 0 0 0 6px rgba(0,0,0,0.3);
}
</style>